/*===== GOOGLE FONTS =====*/
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap');

/*===== VARIABLES CSS =====*/
:root{
    --header-height: 3rem;
    --nav-width: 68px;

    /*===== Colors =====*/
    --first-color: #293D7F;
    --first-color-light: #AFA5D9;
    // --white-color: #F7F6FB;
    --sub-color: #B6CEFC;

    /*===== Font and typography =====*/
    --body-font: 'Roboto';
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
  
    /*===== z index =====*/
    --z-fixed: 100;
}

/*===== BASE =====*/
*,::before,::after{
    box-sizing: border-box;
}

body{
    position: relative;
    // margin: var(--header-height) 0 0 0;
    // padding: 0 1rem;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    transition: .5s;
}

a{
    text-decoration: none;
}

/*===== HEADER =====*/
.header{
    width: 100%;
    height: var(--header-height);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    background-color: var(--white-color);
    z-index: var(--z-fixed);
    transition: .5s;

    &__toggle{
        color: var(--first-color);
        font-size: 1.5rem;
        cursor: pointer;
    }
    &__img{
        width: 35px;
        height: 35px;
        display: flex;
        justify-content: center;
        border-radius: 50%;
        overflow: hidden;

        & img{
            width: 40px;
        }
    }
}

/*===== NAV =====*/
.l-navbar{
    position: fixed;
    top: 0;
    left: -30%;
    width: var(--nav-width);
    height: 100vh;
    background-color: var(--first-color);
    padding: .5rem 1rem 0 0;
    transition: .5s;
    z-index: var(--z-fixed);
}

.nav{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;

    &__logo, &__link{
        display: grid;
        grid-template-columns: max-content max-content;
        align-items: center;
        column-gap: 1rem;
        padding: .5rem 0 .5rem 1.5rem;
    }
    &__logo{
        margin-bottom: 2rem;

        &-icon{
            font-size: 1.25rem;
            color: #F7F6FB;
        }
        &-name{
            color: #F7F6FB;
            font-weight: 700;
        }
    }

    &__link{
        position: relative;
        color: var(--first-color-light);
        margin-bottom: 1rem;
        transition: .3s;
        
        &:hover{
            color: #F7F6FB;
            text-decoration: none;
        }

    }

    &__icon{
        font-size: 1.25rem;
    }
}
a:hover{
    background: #64A3C6;

}
/*Show navbar movil*/
.show{
    left: 0;
}

/*Add padding body movil*/
.body-pd{
    padding-left: calc(var(--nav-width) + 1rem);
}

/*Active links*/
.act{
    color: #F7F6FB;
    &::before{
        content: '';
        position: absolute;
        left: 0;
        width: 2px;
        height: 32px;
        // background-color: #F7F6FB;
    }
}

.active{
    grid-template-columns: 20px max-content 1fr;
    
    &_link{
      justify-self: flex-end;
      transition: .5s;
    }

    &_menu{
      // display: none;
      margin: 0;
      padding: 0;
      padding: .75rem 2.25rem;
    }
    
    &_sublink{
      color: var(--sub-color);
      font-size: var(--small-font-size);
      
      &:hover{
        color: var(--white-color);
      }
    }
}

