@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-blue{background-color:#293D7F}:root{--header-height: 3rem;--nav-width: 68px;--first-color: #293D7F;--first-color-light: #AFA5D9;--sub-color: #B6CEFC;--body-font: 'Roboto';--normal-font-size: 1rem;--small-font-size: .875rem;--z-fixed: 100}*,::before,::after{box-sizing:border-box}body{position:relative;font-family:'Roboto';font-family:var(--body-font);font-size:1rem;font-size:var(--normal-font-size);transition:.5s}a{text-decoration:none}.header{width:100%;height:3rem;height:var(--header-height);position:fixed;top:0;left:0;display:flex;align-items:center;justify-content:space-between;padding:0 1rem;background-color:var(--white-color);z-index:100;z-index:var(--z-fixed);transition:.5s}.header__toggle{color:#293D7F;color:var(--first-color);font-size:1.5rem;cursor:pointer}.header__img{width:35px;height:35px;display:flex;justify-content:center;border-radius:50%;overflow:hidden}.header__img img{width:40px}.l-navbar{position:fixed;top:0;left:-30%;width:68px;width:var(--nav-width);height:100vh;background-color:#293D7F;background-color:var(--first-color);padding:.5rem 1rem 0 0;transition:.5s;z-index:100;z-index:var(--z-fixed)}.nav{height:100%;display:flex;flex-direction:column;justify-content:space-between;overflow:hidden}.nav__logo,.nav__link{display:grid;grid-template-columns:-webkit-max-content -webkit-max-content;grid-template-columns:max-content max-content;align-items:center;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;padding:.5rem 0 .5rem 1.5rem}.nav__logo{margin-bottom:2rem}.nav__logo-icon{font-size:1.25rem;color:#F7F6FB}.nav__logo-name{color:#F7F6FB;font-weight:700}.nav__link{position:relative;color:#AFA5D9;color:var(--first-color-light);margin-bottom:1rem;transition:.3s}.nav__link:hover{color:#F7F6FB;text-decoration:none}.nav__icon{font-size:1.25rem}a:hover{background:#64A3C6}.show{left:0}.body-pd{padding-left:calc(68px + 1rem);padding-left:calc(var(--nav-width) + 1rem)}.act{color:#F7F6FB}.act::before{content:'';position:absolute;left:0;width:2px;height:32px}.active{grid-template-columns:20px -webkit-max-content 1fr;grid-template-columns:20px max-content 1fr}.active_link{justify-self:flex-end;transition:.5s}.active_menu{margin:0;padding:0;padding:.75rem 2.25rem}.active_sublink{color:#B6CEFC;color:var(--sub-color);font-size:.875rem;font-size:var(--small-font-size)}.active_sublink:hover{color:var(--white-color)}.showCollapse{display:block}.rotate{transform:rotate(180deg)}@media screen and (min-width: 768px){.header{height:calc(3rem + 1rem);height:calc(var(--header-height) + 1rem);padding:0 2rem 0 calc(68px + 2rem);padding:0 2rem 0 calc(var(--nav-width) + 2rem)}.header__img{width:40px;height:40px}.header__img img{width:45px}.l-navbar{left:0;padding:1rem 1rem 0 0}.show{width:calc(68px + 156px);width:calc(var(--nav-width) + 156px)}.body-pd{padding-left:calc(68px + 188px);padding-left:calc(var(--nav-width) + 188px)}}.sub-nav{background:#293D7F;margin-top:60px;margin-left:208px}.ListGroup-Item{background:#F1F1F1;margin-bottom:10px;margin-left:220px;width:940px}.img-logo{width:160px;height:130px}.logo{background:white}

